@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  height: 100%;

  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5em;

  color: rgba(0, 0, 0, 0.87);
  background: #f5f5f5;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  position: fixed;
  top: 50%;
  left: 50%;

  display: flex;
  flex-direction: column;

  transform: translate(-50%, -50%);
}

.Form {
  display: inline-flex;
  align-items: flex-end;
}

.Form-control {
  display: flex;
  flex-direction: column;
  max-width: 170px;
  margin-top: 10px;
}

.Form-control:not(:first-child) {
  margin-left: 20px;
}

[class^='Form-control__'] {
  margin-top: 5px;
  padding: 10px;

  border: 1px solid #000000;
  border-radius: 5px;
}

[class*='button'] {
  margin-left: 20px;

  background-color: #ffffff;

  cursor: pointer;
}

.Form-control {
  width: 100%;
  max-width: none;
  max-width: initial;
}

.Table {
  width: 100%;
  margin-top: 20px;

  border-collapse: collapse;
}

.Table th {
  padding-bottom: 15px;

  font-weight: 400;
}

.Table tbody {
  background-color: #ffffff;
  border: 1px solid #000000;
}

.TableRow a {
  margin-right: 5px;

  font-size: 18px;

  color: #000000;
  text-decoration: none;
}

.TableRow td:not(:first-child) {
  text-align: center;
}

.TableRow td {
  padding: 20px;
}

.TableRow:nth-child(even) {
  background-color: #f3f3f3;
}

[colspan="3"] {
  text-align: center;
}

